// app/projects/[projectId]/page.js
"use client";

import React, { useState } from "react";
import { format } from "date-fns";
import {
  X,
  Plus,
  Calendar,
  User,
  Clock,
  Tag,
  CheckCircle2,
  Circle,
  Trash2,
  Edit,
  ChevronDown,
  AlertCircle,
} from "lucide-react";

import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetFooter } from "@/components/ui/sheet";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { toast } from "sonner";

const initialTasks = [
  {
    id: 4545,
    title: "Design new marketing landing page",
    description: "Create high-converting landing page with hero video, testimonials, and CTA.",
    assignee: "Sarah Chen",
    priority: "High",
    status: "In Progress",
    startDate: "2025-11-20",
    dueDate: "2025-11-30",
    tags: ["Design", "Marketing"],
    progress: 65,
    subtasks: [
      { id: 1, title: "Create wireframe", done: true },
      { id: 2, title: "Design hero section", done: true },
      { id: 3, title: "Add testimonials", done: false },
      { id: 4, title: "Mobile optimization", done: false },
    ],
    comments: 3,
  },
  {
    id: 4546,
    title: "Fix payment gateway timeout",
    description: "Users getting timeout error during checkout on high load.",
    assignee: "Mike Torres",
    priority: "High",
    status: "Open",
    startDate: null,
    dueDate: "2025-11-26",
    tags: ["Bug", "Critical"],
    progress: 0,
    subtasks: [],
    comments: 0,
  },
];

export default function TeamworkClone() {
  const [tasks, setTasks] = useState(initialTasks);
  const [selectedTask, setSelectedTask] = useState(null);
  const [sheetOpen, setSheetOpen] = useState(false);
  const [mode, setMode] = useState("view"); // view | create | edit

  const [formData, setFormData] = useState({
    title: "",
    description: "",
    assignee: "",
    priority: "Medium",
    status: "Open",
    startDate: "",
    dueDate: "",
    tags: [],
    subtasks: [],
  });

  const openSheet = (task = null, action = "view") => {
    if (action === "create") {
      setMode("create");
      setFormData({
        title: "",
        description: "",
        assignee: "",
        priority: "Medium",
        status: "Open",
        startDate: "",
        dueDate: "",
        tags: [],
        subtasks: [],
      });
    } else if (action === "edit" && task) {
      setMode("edit");
      setFormData({ ...task });
    } else {
      setMode("view");
    }
    setSelectedTask(task);
    setSheetOpen(true);
  };

  const closeSheet = () => {
    setSheetOpen(false);
    setSelectedTask(null);
    setMode("view");
  };

  const saveTask = () => {
    if (mode === "create") {
      const newTask = {
        ...formData,
        id: Date.now(),
        progress: 0,
        comments: 0,
      };
      setTasks([newTask, ...tasks]);
      toast.success("Task created successfully!");
    } else if (mode === "edit") {
      setTasks(tasks.map(t => t.id === selectedTask.id ? { ...formData } : t));
      toast.success("Task updated!");
    }
    closeSheet();
  };

  const deleteTask = () => {
    setTasks(tasks.filter(t => t.id !== selectedTask.id));
    toast.success("Task deleted");
    closeSheet();
  };

  return (
    <>
      <div className="min-h-screen bg-gray-50">
        <div className="p-8">
          <div className="max-w-7xl mx-auto">
            <div className="flex justify-between items-center mb-8">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">My Project</h1>
                <p className="text-gray-600 mt-1">Team collaboration • {tasks.length} tasks</p>
              </div>
              <Button onClick={() => openSheet(null, "create")} className="bg-blue-600 hover:bg-blue-700">
                <Plus className="w-5 h-5 mr-2" /> Add Task
              </Button>
            </div>

            <Tabs defaultValue="list" className="mb-6">
              <TabsList>
                <TabsTrigger value="list">List</TabsTrigger>
                <TabsTrigger value="board">Board</TabsTrigger>
                <TabsTrigger value="table">Table</TabsTrigger>
              </TabsList>
            </Tabs>

            {/* Task Table */}
            <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
              <table className="w-full">
                <thead className="bg-gray-50 border-b">
                  <tr>
                    <th className="text-left px-6 py-4 text-sm font-medium text-gray-700">Task</th>
                    <th className="text-left px-6 py-4 text-sm font-medium text-gray-700">Assigned to</th>
                    <th className="text-left px-6 py-4 text-sm font-medium text-gray-700">Dates</th>
                    <th className="text-left px-6 py-4 text-sm font-medium text-gray-700">Priority</th>
                    <th className="text-left px-6 py-4 text-sm font-medium text-gray-700">Progress</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {tasks.map((task) => (
                    <tr
                      key={task.id}
                      onClick={() => openSheet(task, "view")}
                      className="hover:bg-gray-50 cursor-pointer transition"
                    >
                      <td className="px-6 py-5">
                        <div className="flex items-center gap-3">
                          <Circle className="w-4 h-4 text-gray-400" />
                          <div>
                            <div className="font-medium text-gray-900">#{task.id}</div>
                            <div className="text-lg font-semibold">{task.title}</div>
                            {task.tags.map((tag) => (
                              <Badge key={tag} variant="secondary" className="mr-1 mt-1">
                                {tag}
                              </Badge>
                            ))}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-5">
                        <div className="flex items-center gap-2">
                          <Avatar className="w-8 h-8">
                            <AvatarFallback className="bg-blue-600 text-white text-xs">
                              {task.assignee[0]}
                            </AvatarFallback>
                          </Avatar>
                          <span>{task.assignee}</span>
                        </div>
                      </td>
                      <td className="px-6 py-5 text-sm text-gray-600">
                        {task.startDate && task.dueDate
                          ? `${format(new Date(task.startDate), "MMM d")} → ${format(new Date(task.dueDate), "MMM d")}`
                          : "No dates"}
                      </td>
                      <td className="px-6 py-5">
                        <Badge className={task.priority === "High" ? "bg-red-600" : "bg-gray-600"}>
                          {task.priority === "High" && <AlertCircle className="w-3 h-3 mr-1" />}
                          {task.priority}
                        </Badge>
                      </td>
                      <td className="px-6 py-5">
                        <div className="flex items-center gap-3">
                          <div className="w-32 bg-gray-200 rounded-full h-2">
                            <div
                              className="bg-blue-600 h-2 rounded-full transition-all"
                              style={{ width: `${task.progress}%` }}
                            />
                          </div>
                          <span className="text-sm text-gray-600">{task.progress}%</span>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* TEAMWORK-STYLE RIGHT SHEET */}
        <Sheet open={sheetOpen} onOpenChange={setSheetOpen}>
          <SheetContent side="right" className="w-full sm:w-[680px] overflow-y-auto bg-gray-50">
            {selectedTask || mode === "create" ? (
              <>
                <SheetHeader className="border-b bg-white sticky top-0 z-10 px-6 py-4">
                  <div className="flex items-center justify-between">
                    <SheetTitle className="text-2xl font-bold">
                      {mode === "create" ? "New Task" : `#${selectedTask.id} ${selectedTask.title || formData.title}`}
                    </SheetTitle>
                    <div className="flex items-center gap-2">
                      {mode === "view" && (
                        <>
                          <Button variant="ghost" size="icon" onClick={() => openSheet(selectedTask, "edit")}>
                            <Edit className="w-5 h-5" />
                          </Button>
                          <Button variant="ghost" size="icon" onClick={deleteTask} className="text-red-600">
                            <Trash2 className="w-5 h-5" />
                          </Button>
                        </>
                      )}
                      <Button variant="ghost" size="icon" onClick={closeSheet}>
                        <X className="w-5 h-5" />
                      </Button>
                    </div>
                  </div>
                </SheetHeader>

                <div className="p-6 space-y-8">
                  {/* Title & Description */}
                  <div>
                    <Input
                      value={mode === "create" ? formData.title : selectedTask.title}
                      onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                      placeholder="Task title"
                      className="text-2xl font-bold border-0 px-0 focus:ring-0"
                      readOnly={mode === "view"}
                    />
                    <Textarea
                      value={mode === "create" ? formData.description : selectedTask.description}
                      onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                      placeholder="Add a description..."
                      className="mt-4 min-h-32 resize-none border-0 focus:ring-0"
                      readOnly={mode === "view"}
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-6">
                    <div>
                      <Label>Assigned to</Label>
                      <Select
                        value={formData.assignee}
                        onValueChange={(v) => setFormData({ ...formData, assignee: v })}
                        disabled={mode === "view"}
                      >
                        <SelectTrigger className="mt-2">
                          <SelectValue placeholder="Unassigned" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Sarah Chen">Sarah Chen</SelectItem>
                          <SelectItem value="Mike Torres">Mike Torres</SelectItem>
                          <SelectItem value="You">You</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div>
                      <Label>Priority</Label>
                      <Select
                        value={formData.priority}
                        onValueChange={(v) => setFormData({ ...formData, priority: v })}
                        disabled={mode === "view"}
                      >
                        <SelectTrigger className="mt-2">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Low">Low</SelectItem>
                          <SelectItem value="Medium">Medium</SelectItem>
                          <SelectItem value="High">High</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div>
                      <Label>Start Date</Label>
                      <Input
                        type="date"
                        value={formData.startDate}
                        onChange={(e) => setFormData({ ...formData, startDate: e.target.value })}
                        className="mt-2"
                        readOnly={mode === "view"}
                      />
                    </div>

                    <div>
                      <Label>Due Date</Label>
                      <Input
                        type="date"
                        value={formData.dueDate}
                        onChange={(e) => setFormData({ ...formData, dueDate: e.target.value })}
                        className="mt-2"
                        readOnly={mode === "view"}
                      />
                    </div>
                  </div>

                  {/* Subtasks */}
                  <div>
                    <Label>Subtasks</Label>
                    <div className="mt-3 space-y-3">
                      {formData.subtasks.map((sub) => (
                        <div key={sub.id} className="flex items-center gap-3">
                          <Checkbox checked={sub.done} disabled={mode === "view"} />
                          <span className={sub.done ? "line-through text-gray-500" : ""}>{sub.title}</span>
                        </div>
                      ))}
                      {mode !== "view" && (
                        <Button variant="outline" size="sm" className="mt-2">
                          <Plus className="w-4 h-4 mr-2" /> Add subtask
                        </Button>
                      )}
                    </div>
                  </div>

                  {/* Action Buttons */}
                  {(mode === "create" || mode === "edit") && (
                    <SheetFooter className="border-t pt-6 mt-10">
                      <Button variant="outline" onClick={closeSheet}>
                        Cancel
                      </Button>
                      <Button onClick={saveTask} className="bg-blue-600 hover:bg-blue-700">
                        {mode === "create" ? "Create Task" : "Save Changes"}
                      </Button>
                    </SheetFooter>
                  )}
                </div>
              </>
            ) : null}
          </SheetContent>
        </Sheet>
      </div>
    </>
  );
}
